import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Axios from "axios";

import { serverURL } from "../../ServerURL";

export default function DetailPage() {
  const { type, pid } = useParams();
  const nav = useNavigate();

  const item_types = ["liquid", "machine", "coil", "pod"];

  const [purchasing, setPur] = useState(false);
  const [amount, setAmount] = useState(1);

  const init_liquid = [
    {
      liquid_id: "",
      liquid_company: "",
      liquid_name: "",
      liquid_type: "",
      liquid_price: 0,
      liquid_volume: "",
      liquid_sweet: 0,
      liquid_cool: 0,
      liquid_hit: 0,
      liquid_category1: "",
      liquid_category2: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ];
  const init_machine = [
    {
      machine_id: 0,
      machine_company: "",
      machine_name: "",
      machine_price: 0,
      machine_category: "",
      machine_type: "",
      machine_coiltype: "",
      machine_coil: "",
      machine_autodraw: "",
      machine_battery: "",
      machine_power: "",
      machine_charge: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ];
  const init_coil = [
    {
      coil_id: 0,
      coil_company: "",
      coil_name: "",
      coil_price: 0,
      coil_fitmachine: "",
      coil_ohm: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ];
  const init_pod = [
    {
      pod_id: 0,
      pod_company: "",
      pod_name: "",
      pod_price: 0,
      pod_fitmachine: "",
      pod_ohm: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ];

  let queryURI, init_state, getCompany, getName, getPrice;
  switch (type) {
    case item_types[0]: {
      queryURI = `L${pid.padStart(5, "0")}`;
      init_state = init_liquid;
      getCompany = (val) => val.liquid_company;
      getName = (val) => val.liquid_name;
      getPrice = (val) => val.liquid_price;
      break;
    }
    case item_types[1]: {
      queryURI = `M${pid.padStart(5, "0")}`;
      init_state = init_machine;
      getCompany = (val) => val.machine_company;
      getName = (val) => val.machine_name;
      getPrice = (val) => val.machine_price;
      break;
    }
    case item_types[2]: {
      queryURI = `C${pid.padStart(5, "0")}`;
      init_state = init_coil;
      getCompany = (val) => val.coil_company;
      getName = (val) => val.coil_name;
      getPrice = (val) => val.coil_price;
      break;
    }
    case item_types[3]: {
      queryURI = `P${pid.padStart(5, "0")}`;
      init_state = init_pod;
      getCompany = (val) => val.pod_company;
      getName = (val) => val.pod_name;
      getPrice = (val) => val.pod_price;
      break;
    }
    default: {
      break;
    }
  }

  const [parts, setParts] = useState(init_state);

  useEffect(() => {
    async function fetchData() {
      try {
        //      await  
        const res = await Axios.get(
          `${serverURL}/api/detail?item_id=VA_${queryURI}`
        );
        setParts(res.data);
      } catch (e) {
        console.error(e.message);
      }
    }
    if (queryURI) fetchData();
  }, [queryURI]);

  // let val = 1.23456789;
  // val = Math.round(val * 10) / 10;

  // let review = 190;

  const image_base = "https://ando-buck.s3.ap-northeast-2.amazonaws.com";

  return (
    <>
      {queryURI && parts[0] ? (
        <div className={`${purchasing ? "h-screen overflow-hidden" : null}`}>
          <img
            width={258.36}
            src={image_base + parts[0].item_img}
            className="mx-auto"
            alt=""
          />
          <div className="-tracking-[0.28px]">
            <p
              // 
              className="font-medium"
            >
              {getCompany(parts[0])}
            </p>
            <p
              // 
              className="mt-[3px] text-14px"
            >
              {getName(parts[0])}
            </p>
            {/* <p
              className="mt-[4px] text-12px"
              //
            >
               
            </p> */}
            <div className="mt-[8px] font-bold">
              {/* <span //  ??
                className="text-14px text-[#FF4903] inline-block align-bottom mr-[6px]"
              >
                23%
              </span> */}
              <span
              // 
              >
                {getPrice(parts[0]).toLocaleString("ko-KR")}
              </span>
            </div>
            {/* <p
              //  / 
              className="h-[28px] mt-[4px] text-12px"
            >
              <span className="relative text-[#EEEEEE]">
                
                <span
                  className="text-[#FF8E00] absolute left-0 overflow-hidden"
                  style={{ width: `${val * 20}%` }}
                >
                  
                </span>
              </span>
              <span className="ml-[4px]">{val} / </span>
              <span className="underline">{review} </span>
            </p> */}
            <hr className="mt-[32px]" />
            {/*    .  . */}
            <img
              src="/img/detail_image_example.png"
              className="mx-auto"
              alt="  "
            />

            <div className="my-[12px]">
              <p className="font-medium"> </p>
              <p className="mt-[24px] py-[18px] px-[23px] text-left text-14px break-all bg-[#F3F3F3] text-[#1B1B1B]">
                - *    .   
                1~3  ,     . 
                   .
                <br />
                <br /> -<span className="text-orange-400">00,000</span> 
                  
              </p>
            </div>

            {purchasing ? (
              // 
              <div
                className="absolute top-[0px] w-full max-w-sm h-full bg-[rgba(28,28,28)]/80"
                onClick={() => {
                  setPur(false);
                }}
              />
            ) : null}
            {/*   */}
            <div
              className={`w-full max-w-sm bg-white fixed bottom-0 z-20${
                purchasing ? " h-[185px]" : " h-[90px]"
              }`}
            >
              {purchasing ? (
                <div className="mx-[16px]">
                  <div className="flex justify-between my-[15px]">
                    <p className="">{getName(parts[0])}</p>
                    <div className="flex">
                      <p
                        className="w-[30px] h-[30px] rounded-full bg-[#F6F6F6] flex flex-col justify-center items-center"
                        onClick={() => {
                          if (amount > 1) setAmount((cur) => cur - 1);
                        }}
                      >
                        <img
                          src="/imageDB/common/minus.svg"
                          width={13.5}
                          alt=""
                        />
                      </p>
                      <p>
                        <span className="mx-[6px] leading-[18.75px] inline-block align-bottom">
                          {amount}
                        </span>
                      </p>
                      <p
                        className="w-[30px] h-[30px] rounded-full bg-[#F6F6F6] flex flex-col justify-center items-center"
                        onClick={() => setAmount((cur) => cur + 1)}
                      >
                        <img
                          src="/imageDB/common/plus.svg"
                          width={13.5}
                          alt=""
                        />
                      </p>
                    </div>
                  </div>

                  <hr className="border-[#757575]" />

                  <p className="mt-[10px] font-bold text-right text-[#0E0E0E]">
                     {(amount * getPrice(parts[0])).toLocaleString("ko-KR")}
                  </p>
                </div>
              ) : null}

              <div className="mt-[21px] mb-[17px] w-full relative">
                <button
                  className="bg-orange-400 border border-[#E6E6E6] rounded-[8px] w-[161px] h-[52px] absolute left-[16px]"
                  onClick={() => {
                    if (!purchasing) setPur(true);
                    else alert(" !");
                  }}
                >
                  
                </button>
                <button
                  className="bg-black text-white rounded-[8px] w-[161px] h-[52px] absolute right-[16px]"
                  onClick={() => {
                    if (!purchasing) setPur(true);
                    else nav("/purchase/" + pid);
                  }}
                >
                  
                </button>
              </div>
            </div>
          </div>
        </div>
      ) : (
        <h2>   .</h2>
      )}
    </>
  );
}
