import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Axios from "axios";

import { serverURL } from "../../ServerURL";

import FilterMachine from "./FilterMachine";
import SortMachine from "./SortMachine";

export default function ShopMachine({ search_text, setText }) {
  const nav = useNavigate();
  const image_base = "https://ando-buck.s3.ap-northeast-2.amazonaws.com";
  const sortOptions = [
    // "BEST",
    "NEW",
    " ",
    " ",
  ];
  const [toggle_sort, setToggleSorter] = useState(false); //    
  const [sortModeText, setSortModeText] = useState("NEW"); //    
  const [sortMode, setSortMode] = useState("new"); //   

  const [typeFilter, setFilter] = useState(0);
  const filterOptionsMachine = ["", ""];

  const setSortOption = (text) => {
    let mode;
    switch (text) {
      // case "BEST":
      //   mode = "best";
      //   break;
      case "NEW":
        mode = "new";
        break;
      case " ":
        mode = "cheap";
        break;
      case " ":
        mode = "expensive";
        break;
      default:
        mode = "best";
        break;
    }
    setSortMode(mode);
    setSortModeText(text);
    setToggleSorter(false);
  };

  const [parts, setParts] = useState([
    {
      machine_id: "",
      machine_company: "",
      machine_name: "",
      machine_price: 0,
      machine_category: "",
      machine_type: "",
      machine_coiltype: "",
      machine_coil: "",
      machine_autodraw: "",
      machine_battery: "",
      machine_power: "",
      machine_charge: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ]);

  const [query_clause, setQuery] = useState("?");

  useEffect(() => {
    async function fetchData() {
      try {
        //      await  
        const res = await Axios.get(
          `${serverURL}/api/filter/machine${query_clause}search_text=${search_text}&sortMode=${sortMode}`
        );
        setParts(res.data);
      } catch (e) {
        console.error(e.message);
      }
    }
    fetchData();
  }, [query_clause, search_text, sortMode]);

  return (
    <>
      <div
        className="z-10 mt-[12px] mb-[20px] text-14px text-left font-medium text-[#BDBDBD] bg-white relative"
        onClick={() => {
          setToggleSorter((cur) => !cur);
        }}
      >
        <div className="mx-[16px] flex">
          <p></p>
          <p className="mx-[6px] flex flex-col justify-center">
            <img
              src="/imageDB/shop_common/Chevron_Right_5_8_BDBDBD.svg"
              alt="Right"
            />
          </p>
          <p className="text-black">{sortModeText}&nbsp;</p>
          <p className="mx-[4px] flex flex-col justify-center">
            <img
              src="/imageDB/shop_common/Chevron_Down_8_5_black_2.svg"
              alt="Down"
            />
          </p>
        </div>
        <SortMachine
          toggle_sort={toggle_sort}
          sortOptions={sortOptions}
          sortModeText={sortModeText}
          setToggleSorter={setToggleSorter}
          setSortOption={setSortOption}
        />
      </div>

      <hr className="border-[#F2F2F2]" />

      <div className="flex rounded-[17.5px] mt-[20px] mx-[16px]">
        {
          //  
          filterOptionsMachine.map((it, idx) => {
            return (
              <div
                className="w-[76px] h-[32px] mr-[8px] text-14px rounded-[17.5px] bg-[#F4F4F4] px-[8px] py-[4px] flex justify-around"
                // onClick={() => applyFilterOption()}
                onClick={() => setFilter(idx + 1)}
                key={"fliter_liq_" + idx}
              >
                <p>{it}</p>
                <p className="flex flex-col justify-center">
                  <img
                    src="/imageDB/shop_common/Chevron_Down_8_5_4F4F4F_1.5.svg"
                    alt="Down"
                  />
                </p>
              </div>
            );
          })
        }
      </div>

      <div className="mt-[30px] mx-[16px] text-left text-14px grid grid-cols-2 gap-[12px]">
        {parts.map((part, i) => (
          <div
            className="mb-[12px] flex flex-col"
            key={"shop_machine_" + i}
            onClick={() =>
              nav(`/detail/machine/${parseInt(part.machine_id.slice(4))}`)
            }
          >
            <div className="bg-[#F5F5F5] w-full relative">
              <p className="my-[12px]">
                <img
                  src={image_base + part.item_img}
                  width={138}
                  className="mx-auto"
                  alt=""
                />
              </p>
            </div>
            <div className="mt-[12px] text-left tracking-[-0.28px]">
              <p
                className="text-14px font-medium"
                onClick={() => {
                  setText(part.machine_company);
                  window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
                }}
              >
                {part.machine_company}
              </p>
              <p className="text-14px">{part.machine_name}</p>
              <p className="font-bold">
                {part.machine_price.toLocaleString("ko-KR")}
              </p>
              <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                
              </p>
            </div>
          </div>
        ))}
      </div>

      {/*    */}
      <FilterMachine
        typeFilter={typeFilter}
        setFilter={setFilter}
        filterOptionsMachine={filterOptionsMachine}
        setText={setText}
        setQuery={setQuery}
      />
    </>
  );
}
