import { memo } from "react";
import { useNavigate } from "react-router-dom";

const BottomNav = () => {
  const href = window.location.href;
  const nav = useNavigate();

  return (
    <footer className="fixed bottom-0 inset-x-0 mx-auto bg-white w-screen max-w-sm h-[80px] z-20">
      <hr className="border-[#DBDBDB]" />
      <div className="py-[16px] flex items-center mx-[60px]">
        <>
          <div
            className="w-4/12"
            onClick={() => {
              nav("/");
            }}
          >
            <img
              src={
                href.endsWith("http://localhost:3000/") || href.endsWith("/")
                  ? "/imageDB/bottom_nav/home_sel.svg"
                  : "/imageDB/bottom_nav/home_unsel.svg"
              }
              width={18}
              alt="home"
            />
          </div>
          <div
            className="grow"
            onClick={() => {
              nav("/shop");
            }}
          >
            <img
              src={
                href.includes("/shop")
                  ? "/imageDB/bottom_nav/bag_sel.svg"
                  : "/imageDB/bottom_nav/bag_unsel.svg"
              }
              width={24}
              className="mx-auto"
              alt="shoppingbag"
            />
          </div>
          <div
            className="w-4/12"
            onClick={() => {
              nav("/my");
            }}
          >
            <div className="w-[24px] h-[24px] rounded-full bg-[#C6D8EA] ml-auto relative">
              {href.includes("/my") ? (
                <p className="w-[24px] h-[24px] rounded-full border border-black absolute z-10"></p>
              ) : null}
              <p className="w-[10px] h-[10px] rounded-full bg-white absolute inset-x-0 mx-auto top-[3px]"></p>
              <p className="w-[20px] h-[20px] rounded-full bg-white absolute inset-x-0 mx-auto top-[15px]"></p>
            </div>
          </div>
        </>
      </div>
    </footer>
  );
};

export default memo(BottomNav);
