export default function SortLiquid({
  toggle_sort,
  sortOptions,
  sortModeText,
  setToggleSorter,
  setSortOption,
}) {
  return toggle_sort ? (
    <>
      <div
        // 
        className="absolute top-[84px] w-full max-w-sm h-screen bg-[rgba(28,28,28)]/80"
        onClick={(e) => {
          e.stopPropagation();
          setToggleSorter(false);
        }}
      />

      <div
        className="absolute top-[20px] z-10 w-full max-w-sm bg-white"
        onClick={(e) => {
          e.stopPropagation();
        }}
      >
        <div className="w-full max-w-sm py-[12px]">
          {sortOptions.map((opt, itr) => (
            <div
              className="py-[12px]"
              key={"liquid_sort_" + itr}
              onClick={() => {
                if (sortModeText === opt) setToggleSorter(false);
                else setSortOption(opt);
              }}
            >
              <p
                style={sortModeText === opt ? { color: "black" } : null}
                className="px-[16px]"
              >
                {opt}
              </p>
            </div>
          ))}
        </div>
      </div>
    </>
  ) : null;
}
