import { memo } from "react";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";

const Header = () => {
  const href = window.location.href;
  const nav = useNavigate();
  const custom_str = useSelector((state) => state.custom_str);

  const HeadLanding = () => (
    <div
      className="pl-[28px] w-4/12"
      onClick={() => {
        nav(-1);
      }}
    >
      <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
    </div>
  );

  const HeadMain = () => (
    <div className="mx-auto">
      <img src="/img/ANDO.svg" width={56} className="mx-auto" alt="ANDO-Logo" />
      {/* <p className="text-12px text-[#777777]">  </p> */}
    </div>
  );

  const HeadHistory = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
          window.scrollTo(0, 0);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div className="w-4/12 text-18px"> </div>
      <div className="w-4/12" />
    </>
  );

  const HeadLogo = () => (
    <div
      className="mx-auto"
      onClick={() => {
        nav("/");
      }}
    >
      <img src="/img/ANDO.svg" width={56} className="mx-auto" alt="ANDO-Logo" />
      {/* <p className="text-12px text-[#777777]">  </p> */}
    </div>
  );

  const HeadReview = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div
        className="w-4/12 font-medium"
        onClick={() => {
          nav("/");
        }}
      >
        
      </div>
      <div className="w-4/12"></div>
    </>
  );

  const HeadCustom = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div className="w-4/12 Rubik">{custom_str}</div>
      <div className="w-4/12" />
    </>
  );

  const HeadElse = () => (
    <>
      <div
        className="w-4/12 text-left pl-[28px]"
        onClick={() => {
          nav(-1);
        }}
      >
        <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
      </div>

      <div
        className="w-4/12"
        onClick={() => {
          nav("/");
        }}
      >
        <img
          src="/img/ANDO.svg"
          width={56}
          className="mx-auto"
          alt="ANDO-Logo"
        />
        {/* <p className="text-12px text-[#777777]">  </p> */}
      </div>
      <div className="w-4/12"></div>
    </>
  );

  return (
    <header
      className={
        !(!href.endsWith("shop") && href.includes("shop"))
          ? "h-[72px] pt-[22px] pb-[10px] flex items-center"
          : ""
      }
      onClick={(e) => {
        e.stopPropagation();
      }}
    >
      {/*      .  "href.endsWith("/")" . */}
      {href.endsWith("landing") ? null : href.includes("landing") ? (
        <HeadLanding />
      ) : href.endsWith("http://localhost:3000/") || href.endsWith("/") ? (
        <HeadMain />
      ) : href.includes("my/history") ? (
        <HeadCustom />
      ) : href.includes("history") ? (
        <HeadHistory />
      ) : href.endsWith("shop") ? (
        <HeadLogo />
      ) : href.includes("shop") ? null : href.endsWith("my") ? (
        <HeadLogo />
      ) : href.includes("review") ? (
        <HeadReview />
      ) : (
        <HeadElse />
      )}
    </header>
  );
};

export default memo(Header);
