import { useState } from "react";

import { serverURL } from "../../ServerURL";

import {
  faChevronDown,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function ShopAccessory({ search_text, setText }) {
  const sortOptions = [
    // "BEST",
    "NEW",
    " ",
    " ",
  ];
  const [toggle_sort, setToggleSorter] = useState(false); //    
  const [sortModeText, setSortModeText] = useState("NEW"); //    
  const [sortMode, setSortMode] = useState("new"); //   

  const setSortOption = (text) => {
    let mode;
    switch (text) {
      //   case "BEST":
      //     mode = "best";
      //     break;
      case "NEW":
        mode = "new";
        break;
      case " ":
        mode = "cheap";
        break;
      case " ":
        mode = "expensive";
        break;
      default:
        mode = "new";
        break;
    }
    setSortMode(mode);
    setSortModeText(text);
    setToggleSorter(false);
  };

  const parts = new Array(14).fill(0);

  return (
    <div className={`${toggle_sort ? "h-screen overflow-hidden" : null}`}>
      <div
        className="z-10 mt-[12px] mb-[20px] text-14px text-left font-medium text-[#BDBDBD] bg-white relative"
        onClick={() => {
          setToggleSorter((cur) => !cur);
        }}
      >
        <div className="mx-[16px] flex">
          <p></p>
          <p className="mx-[6px] flex flex-col justify-center">
            <img
              src="/imageDB/shop_common/Chevron_Right_5_8_BDBDBD.svg"
              alt="Right"
            />
          </p>
          <p className="text-black">{sortModeText}&nbsp;</p>
          <p className="mx-[4px] flex flex-col justify-center">
            <img
              src="/imageDB/shop_common/Chevron_Down_8_5_black_2.svg"
              alt="Down"
            />
          </p>
        </div>
        {toggle_sort ? (
          <div
            className="absolute top-[20px] z-10 w-full max-w-sm bg-white"
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            <div className="w-full max-w-sm py-[12px]">
              {sortOptions.map((opt, itr) => (
                <div
                  className="py-[12px]"
                  key={"liquid_sort_" + itr}
                  onClick={() => {
                    if (sortModeText === opt) setToggleSorter(false);
                    else setSortOption(opt);
                  }}
                >
                  <p
                    style={sortModeText === opt ? { color: "black" } : null}
                    className="px-[16px]"
                  >
                    {opt}
                  </p>
                </div>
              ))}
            </div>
          </div>
        ) : null}
      </div>

      <hr className="border-[#F2F2F2]" />

      <div className="mt-[30px] mx-[16px] text-left text-14px grid grid-cols-2 gap-[12px]">
        {parts.map((part, i) => (
          <div className="mb-[12px] flex flex-col" key={"shop_liquid_" + i}>
            <div className="bg-[#F5F5F5] w-full relative">
              <p className="my-[12px]">
                <img
                  src="/imageDB/pot_trace/vthru-pro-0.7.png"
                  width={138}
                  className="mx-auto"
                  alt=""
                />
              </p>
            </div>
            <div className="mt-[12px] text-left tracking-[-0.28px]">
              <p className="text-14px font-medium"></p>
              <p className="text-14px"> 0.7</p>
              <p className="font-bold">10</p>
              <p className="mt-[4px] py-[1px] px-[4px] text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                
              </p>
            </div>
          </div>
        ))}
      </div>

      {toggle_sort ? (
        // 
        <div
          className="absolute top-[84px] w-full max-w-sm h-full bg-[rgba(28,28,28)]/80"
          onClick={() => setToggleSorter(false)}
        />
      ) : null}
    </div>
  );
}
