import Range from "rc-slider";
import { useState } from "react";
import "../../css/rc-slider.css";

export default function FilterLiquid({
  typeFilter,
  setFilter,
  filterOptionsLiquid,
  setText,
  setQuery,
}) {
  const list_brand = [
    "1%",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const list_taste = ["", "", ""];
  const intensity_taste = ["", "", "", ""];
  const list_category1 = [
    { str: "", img: "/imageDB/shop_liquid/Fruit.png" },
    // { str: " ", img: "/imageDB/shop_liquid/Menthol.png" },
    { str: "", img: "/imageDB/shop_liquid/Dessert.png" },
    { str: "", img: "/imageDB/shop_liquid/Beverage.png" },
    { str: "", img: "/imageDB/shop_liquid/Cigarette.png" },
  ];

  const [select_brand, setBrand] = useState(
    new Array(list_brand.length).fill(false)
  );
  const [min_taste, setTasteMin] = useState(
    new Array(list_taste.length).fill(0)
  );
  const [max_taste, setTasteMax] = useState(
    new Array(list_taste.length).fill(intensity_taste.length - 1)
  );
  const [select_category1, setCategory1] = useState(
    new Array(list_category1.length).fill(false)
  );

  const MakeQuery = () => {
    let query_brand = "";
    for (let i = 0; i < select_brand.length; i++) {
      if (select_brand[i]) {
        if (query_brand) query_brand += `,${list_brand[i]}`;
        else query_brand += `liquid_company=${list_brand[i]}`;
      }
    }

    let query_min_taste = "";
    for (let i = 0; i < min_taste.length; i++) {
      if (!(min_taste[i] === 0 && max_taste[i] === 3)) {
        if (query_min_taste)
          query_min_taste += `&liquid_min_taste_${i}=${min_taste[i]}`;
        else query_min_taste += `liquid_min_taste_${i}=${min_taste[i]}`;
      }
    }

    let query_max_taste = "";
    for (let i = 0; i < max_taste.length; i++) {
      if (!(min_taste[i] === 0 && max_taste[i] === 3)) {
        if (query_max_taste)
          query_max_taste += `&liquid_max_taste_${i}=${max_taste[i]}`;
        else query_max_taste += `liquid_max_taste_${i}=${max_taste[i]}`;
      }
    }

    let query_category1 = "";
    for (let i = 0; i < select_category1.length; i++) {
      if (select_category1[i]) {
        if (query_category1) query_category1 += `,${list_category1[i]["str"]}`;
        else query_category1 += `liquid_category1=${list_category1[i]["str"]}`;
      }
    }

    let query_final = "?";
    query_final += query_brand ? query_brand : "";
    query_final += query_min_taste
      ? query_final.length > 1
        ? `&${query_min_taste}`
        : query_min_taste
      : "";
    query_final += query_max_taste
      ? query_final.length > 1
        ? `&${query_max_taste}`
        : query_max_taste
      : "";
    query_final += query_category1
      ? query_final.length > 1
        ? `&${query_category1}`
        : query_category1
      : "";
    query_final += query_final.length > 1 ? "&" : "";

    //       
    //      .
    query_final = encodeURI(query_final);
    setQuery(query_final);
  };

  const ModalBrand = () => (
    <div className="overflow-y-scroll hide-scrollbar h-[345px]">
      {list_brand.map((elem, itr) => (
        <div key={"liquid_brand_" + itr}>
          <div
            className="flex justify-between px-[16px] pt-[7px] pb-[8px]"
            onClick={() => {
              let arr = select_brand;
              arr[itr] = !arr[itr];
              setBrand([...arr]);
            }}
          >
            <p className={`${select_brand[itr] ? "text-black" : ""}`}>{elem}</p>
            <p>
              {select_brand[itr] ? (
                <img
                  src="/imageDB/common/checked.svg"
                  width={16}
                  alt="checked"
                />
              ) : (
                <img
                  src="/imageDB/common/unchecked.svg"
                  width={16}
                  alt="unchecked"
                />
              )}
            </p>
          </div>
          {itr !== list_brand.length - 1 ? (
            <hr className="mt-[8px] mb-[7px] border-[#F4F4F4]" />
          ) : null}
        </div>
      ))}
    </div>
  );

  const ModalTaste = () => (
    <div className="mx-[16px]">
      {list_taste.map((elem, itr) => (
        <div
          className="w-full h-[89px] bg-[#F5F5F5] rounded-[12px] mb-[10px]"
          key={"liquid_taste_" + itr}
        >
          <p className="pt-[10px] pb-[8px] text-black font-bold">{elem}</p>
          <div>
            <Range
              range
              allowCross={false}
              min={0}
              max={3}
              defaultValue={[min_taste[itr], max_taste[itr]]}
              marks={intensity_taste}
              onAfterChange={(val) => {
                let arr_min = min_taste;
                arr_min[itr] = val[0];
                let arr_max = max_taste;
                arr_max[itr] = val[1];
                setTasteMin([...arr_min]);
                setTasteMax([...arr_max]);
              }}
            />
          </div>
        </div>
      ))}
    </div>
  );

  const ModalCategory1 = () => (
    <div className="overflow-y-scroll hide-scrollbar h-[345px]">
      {list_category1.map((elem, itr) => (
        <div key={"liquid_category1_" + itr}>
          <div
            className="flex justify-between px-[16px] pt-[7px] pb-[8px]"
            onClick={() => {
              let arr = select_category1;
              arr[itr] = !arr[itr];
              setCategory1([...arr]);
            }}
          >
            <p className={`flex${select_category1[itr] ? " text-black" : ""}`}>
              <img
                src={elem.img}
                width={24}
                className="mr-[8px]"
                alt={elem.str}
              />
              {elem.str}
            </p>
            <div>
              <p>
                {select_category1[itr] ? (
                  <img
                    src="/imageDB/common/checked.svg"
                    width={16}
                    alt="checked"
                  />
                ) : (
                  <img
                    src="/imageDB/common/unchecked.svg"
                    width={16}
                    alt="unchecked"
                  />
                )}
              </p>
            </div>
          </div>
          {itr !== list_category1.length - 1 ? (
            <hr className="mt-[8px] mb-[7px] border-[#F4F4F4]" />
          ) : null}
        </div>
      ))}
    </div>
  );

  return typeFilter ? (
    <>
      {/*  */}
      <div
        className="absolute top-0 z-10 w-full max-w-sm h-screen bg-[rgba(28,28,28)]/80"
        onClick={() => {
          setFilter(0);
        }}
      />
      <div className="bg-white w-full max-w-sm h-[518px] rounded-t-[20px] fixed z-20 bottom-0">
        <div
          className="w-[40px] h-[5px] rounded-[2.5px] bg-[#F0F0F0] mx-auto mt-[10px]"
          onClick={() => {
            setFilter(0);
          }}
        />

        <div className="mt-[25px] flex font-bold text-[#C3C3C3] mb-[28px]">
          {/*   */}
          {filterOptionsLiquid.map((elem, itr) => (
            <p
              className={`ml-[16px] ${
                typeFilter === itr + 1 ? "text-black" : null
              }`}
              key={"liquid_filter_tab_" + itr}
              onClick={() => setFilter(itr + 1)}
            >
              {elem}
            </p>
          ))}
        </div>
        <div className="text-[#A0A0A0] font-medium text-14px">
          {typeFilter === 1 ? (
            <ModalBrand />
          ) : typeFilter === 2 ? (
            <ModalTaste />
          ) : typeFilter === 3 ? (
            <ModalCategory1 />
          ) : null}
          {/* <div className="absolute bottom-[81px] w-full mt-[12px] flex bg-teal-500"> */}
          {/*    !! */}
          {/* {["", "", ""].map((elem) => (
            <div className="bg-black rounded-[18.5px] text-white w-[79px] h-[37px] mr-[8px] flex flex-col justify-center">
              <p>{elem}</p>
            </div>
          ))}
        </div> */}
        </div>
        <div className="w-full max-w-sm h-[81px] flex absolute bottom-0">
          <div className="mt-[12px] mb-[17px] w-full relative">
            <button
              className="bg-white border border-[#E6E6E6] rounded-[8px] w-[114px] h-[52px] absolute left-[16px]"
              onClick={() => {
                setBrand(new Array(list_brand.length).fill(false));
                setTasteMin(new Array(list_taste.length).fill(0));
                setTasteMax(
                  new Array(list_taste.length).fill(intensity_taste.length - 1)
                );
                setCategory1(new Array(list_category1.length).fill(false));
                setText("");
              }}
            >
              
            </button>
            <button
              className="bg-black text-white rounded-[8px] w-[208px] h-[52px] absolute right-[16px]"
              onClick={() => {
                //     
                MakeQuery();
                setFilter(0);
              }}
            >
              
            </button>
          </div>
        </div>
      </div>
    </>
  ) : null;
}
