import { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function HeadShop({ setText }) {
  const nav = useNavigate();
  const [searchbar, setSearch] = useState(false);
  const [text_input, setInput] = useState("");

  return (
    <header
      className="h-[72px] pt-[22px] pb-[10px] flex items-center"
      onClick={(e) => {
        e.stopPropagation();
      }}
    >
      {!searchbar ? (
        <>
          <div
            className="w-4/12 text-left pl-[28px]"
            onClick={() => {
              nav(-1);
            }}
          >
            <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
          </div>
          <div
            className="w-4/12"
            onClick={() => {
              nav("/");
            }}
          >
            <img
              src="/img/ANDO.svg"
              width={56}
              className="mx-auto"
              alt="ANDO-Logo"
            />
            {/* <p className="text-12px text-[#777777]">  </p> */}
          </div>

          <div className="w-4/12">
            <div
              className="w-[40px] px-[8px] ml-auto"
              onClick={() => {
                setSearch(true);
              }}
            >
              <img
                src="/imageDB/header/icon_search.svg"
                width={21}
                alt="search"
              />
            </div>
          </div>
        </>
      ) : (
        <>
          <div
            className="w-[57px] text-left pl-[28px] py-[8px]"
            onClick={() => {
              setSearch(false);
            }}
          >
            <img src="/imageDB/header/Chevron_Left.svg" width={11} alt="back" />
          </div>

          <div className="w-full">
            <form
              className="w-full flex relative"
              onSubmit={(e) => {
                e.preventDefault();
                setText(text_input);
                setSearch(false);
                // DB  
              }}
            >
              <input
                type="text"
                onChange={(e) => {
                  setInput(e.target.value);
                }}
                placeholder="  ."
                required={true}
                className="ml-auto mr-[16px] w-full h-[40px] rounded-[40px] pl-[17px] bg-[#F2F2F2]"
              />

              <button
                type="submit"
                className="px-[12px] self-center absolute right-[16px]"
              >
                <img
                  src="/imageDB/header/icon_search.svg"
                  width={21}
                  alt="search"
                />
              </button>
            </form>
          </div>
        </>
      )}
    </header>
  );
}
