import { useState } from "react";

export default function FilterMachine({
  typeFilter,
  setFilter,
  filterOptionsMachine,
  setText,
  setQuery,
}) {
  const list_coil = [
    { title: "", detail: "    " },
    { title: "", detail: " ,     " },
  ];
  const list_draw = [
    {
      title: "   ",
      detail: "    ",
      autodraw: "y",
    },
    { title: "   ", detail: null, autodraw: "n" },
  ];
  const list_brand = [
    "",
    "",
    // "",
    "",
    "",
    // "",
    "",
  ];

  const [select_coil, setCoil] = useState(
    new Array(list_coil.length).fill(false)
  );
  const [select_draw, setDraw] = useState(
    new Array(list_draw.length).fill(false)
  );
  const [select_brand, setBrand] = useState(
    new Array(list_brand.length).fill(false)
  );

  const MakeQuery = () => {
    let query_coil = "";
    for (let i = 0; i < select_coil.length; i++) {
      if (select_coil[i]) {
        if (query_coil) query_coil += `,${list_coil[i]["title"]}`;
        else query_coil += `machine_coiltype=${list_coil[i]["title"]}`;
      }
    }

    let query_draw = "";
    for (let i = 0; i < select_draw.length; i++) {
      if (select_draw[i]) {
        if (query_draw) query_draw += `,${list_draw[i]["autodraw"]}`;
        else query_draw += `machine_autodraw=${list_draw[i]["autodraw"]}`;
      }
    }

    let query_brand = "";
    for (let i = 0; i < select_brand.length; i++) {
      if (select_brand[i]) {
        if (query_brand) query_brand += `,${list_brand[i]}`;
        else query_brand += `machine_company=${list_brand[i]}`;
      }
    }

    let query_final = "?";
    query_final += query_coil ? query_coil : "";
    query_final += query_draw
      ? query_final.length > 1
        ? `&${query_draw}`
        : query_draw
      : "";
    query_final += query_brand
      ? query_final.length > 1
        ? `&${query_brand}`
        : query_brand
      : "";
    query_final += query_final.length > 1 ? "&" : "";

    //       
    //      .
    query_final = encodeURI(query_final);
    setQuery(query_final);
  };

  const ModalFeature = () => (
    <div className="overflow-y-scroll hide-scrollbar h-[345px]">
      <div className="font-bold text-left text-[#2B2B2B] mx-[16px]">
        , 
        <hr className="mt-[14px] mb-[7px] border-[#F4F4F4]" />
      </div>
      {list_coil.map((elem, itr) => (
        <div key={"machine_coil_" + itr}>
          <div
            className="flex justify-between px-[16px] pt-[7px] pb-[8px]"
            onClick={() => {
              let arr = select_coil;
              arr[itr] = !arr[itr];
              setCoil([...arr]);
            }}
          >
            <div className="flex">
              <p className={`${select_coil[itr] ? "text-black" : ""}`}>
                {elem.title}
              </p>
              <p className="ml-[12px] self-center font-normal text-12px">
                {elem.detail}
              </p>
            </div>
            <p>
              {select_coil[itr] ? (
                <img
                  src="/imageDB/common/checked.svg"
                  width={16}
                  alt="checked"
                />
              ) : (
                <img
                  src="/imageDB/common/unchecked.svg"
                  width={16}
                  alt="unchecked"
                />
              )}
            </p>
          </div>
        </div>
      ))}
      <div className="font-bold text-left text-[#2B2B2B] mx-[16px] mt-[25px]">
         
        <hr className="mt-[14px] mb-[7px]" />
      </div>
      {list_draw.map((elem, itr) => (
        <div key={"machine_draw_" + itr}>
          <div
            className="flex justify-between px-[16px] pt-[7px] pb-[8px]"
            onClick={() => {
              let arr = select_draw;
              arr[itr] = !arr[itr];
              setDraw([...arr]);
            }}
          >
            <div className="flex">
              <p className={`${select_draw[itr] ? "text-black" : ""}`}>
                {elem.title}
              </p>
              <p className="ml-[12px] self-center font-normal text-12px">
                {elem.detail}
              </p>
            </div>
            <p>
              {select_draw[itr] ? (
                <img
                  src="/imageDB/common/checked.svg"
                  width={16}
                  alt="checked"
                />
              ) : (
                <img
                  src="/imageDB/common/unchecked.svg"
                  width={16}
                  alt="unchecked"
                />
              )}
            </p>
          </div>
        </div>
      ))}
    </div>
  );

  const ModalBrand = () => (
    <div className="overflow-y-scroll hide-scrollbar h-[345px]">
      {list_brand.map((elem, itr) => (
        <div key={"machine_brand_" + itr}>
          <div
            className="flex justify-between px-[16px] pt-[7px] pb-[8px]"
            onClick={() => {
              let arr = select_brand;
              arr[itr] = !arr[itr];
              setBrand([...arr]);
            }}
          >
            <p className={`${select_brand[itr] ? "text-black" : ""}`}>{elem}</p>
            <p>
              {select_brand[itr] ? (
                <img
                  src="/imageDB/common/checked.svg"
                  width={16}
                  alt="checked"
                />
              ) : (
                <img
                  src="/imageDB/common/unchecked.svg"
                  width={16}
                  alt="unchecked"
                />
              )}
            </p>
          </div>
          {itr !== list_brand.length - 1 ? (
            <hr className="mt-[8px] mb-[7px] border-[#F4F4F4]" />
          ) : null}
        </div>
      ))}
    </div>
  );

  return typeFilter ? (
    <>
      {/*  */}
      <div
        className="absolute top-0 z-10 w-full max-w-sm h-screen bg-[rgba(28,28,28)]/80"
        onClick={() => {
          setFilter(0);
        }}
      />

      <div className="bg-white w-full max-w-sm h-[518px] rounded-t-[20px] fixed z-20 bottom-0">
        <div
          className="w-[40px] h-[5px] rounded-[2.5px] bg-[#F0F0F0] mx-auto mt-[10px]"
          onClick={() => {
            setFilter(0);
          }}
        />

        <div className="mt-[25px] mb-[28px] flex font-bold text-[#C3C3C3]">
          {/*   */}
          {filterOptionsMachine.map((elem, itr) => (
            <p
              className={`ml-[16px] ${
                typeFilter === itr + 1 ? "text-black" : null
              }`}
              key={"machine_filter_tab_" + itr}
              onClick={() => setFilter(itr + 1)}
            >
              {elem}
            </p>
          ))}
        </div>
        <div className="text-[#A0A0A0] font-medium text-14px">
          {typeFilter === 1 ? (
            <ModalFeature />
          ) : typeFilter === 2 ? (
            <ModalBrand />
          ) : null}
          {/* <div className="absolute bottom-[81px] w-full mt-[12px] flex bg-teal-500"> */}
          {/*    !! */}
          {/* {["", "", ""].map((elem) => (
            <div className="bg-black rounded-[18.5px] text-white w-[79px] h-[37px] mr-[8px] flex flex-col justify-center">
              <p>{elem}</p>
            </div>
          ))}
        </div> */}
        </div>
        <div className="w-full max-w-sm h-[81px] flex absolute bottom-0">
          <div className="mt-[12px] mb-[17px] w-full relative">
            <button
              className="bg-white border border-[#E6E6E6] rounded-[8px] w-[114px] h-[52px] absolute left-[16px]"
              onClick={() => {
                setBrand(new Array(list_brand.length).fill(false));
                setCoil(new Array(list_coil.length).fill(false));
                setDraw(new Array(list_draw.length).fill(false));
                setText("");
              }}
            >
              
            </button>
            <button
              className="bg-black text-white rounded-[8px] w-[208px] h-[52px] absolute right-[16px]"
              onClick={() => {
                //     
                MakeQuery();
                setFilter(0);
              }}
            >
              
            </button>
          </div>
        </div>
      </div>
    </>
  ) : null;
}
