import { memo, useState } from "react";

import Liq from "./MainShopping/Liq";
import Acc from "./MainShopping/Acc";
import MyDev from "./MyDev";
import { useNavigate } from "react-router-dom";

const MyAndo = () => {
  const nav = useNavigate();

  const [acc, setAcc] = useState(false);
  const [liq, setLiq] = useState(false);
  const [myD, setD] = useState(false);

  // let manu; // .   DB    
  // let dev; // .   DB    
  let manu = ""; // .   DB    
  let dev = " Q"; // .   DB    
  // let dev = null;

  let manu_text = manu ?? "";
  let dev_text = dev ?? "";
  let acc_list = new Array(30).fill(0); // 
  let liq_list = new Array(30).fill(0); // 

  const handleClick = (type, bool) => {
    if (type === "acc") setAcc(bool);
    else setLiq(bool);

    if (!dev) setD(bool);
  };

  //  dev      
  return (
    <div className="h-[440px] relative overflow-hidden">
      {!(dev || acc || liq) ? (
        <div className="absolute inset-x-0 mx-auto z-10 bg-[#F5F4F1]/50 backdrop-blur-[2px] mt-[150px] w-[248px] h-[75px] rounded-[17px] ando-animation">
          <p className="pt-[8px] text-14px text-[#5B5845] font-medium">
             .
          </p>
          <p
            className="rounded-[16px] bg-[#989384] w-min text-white text-14px font-medium py-[6px] px-[20px] mt-[4px] mx-auto"
            onClick={() => nav("/machine")}
          >
            &nbsp;
          </p>
        </div>
      ) : null}

      <MyDev dev_text={dev_text} manu_text={manu_text} dev={dev} myD={myD} />

      <div className="mt-[24px] flex w-min mx-auto gap-3">
        <div
          className="w-[100px] rounded-full h-[100px] border border-solid border-[#F2F2F2] flex flex-col justify-center items-center"
          onClick={() => {
            handleClick("acc", true);
          }}
        >
          <img src="/imageDB/my_ando/acc.svg" width={40} alt="" />
          <p className="text-12px mt-[8px]"></p>
        </div>
        <div
          className="w-[100px] rounded-full h-[100px] border border-solid border-[#F2F2F2] flex flex-col justify-center items-center"
          onClick={() => {
            handleClick("liq", true);
          }}
        >
          <span id="Liq" />
          <p className="text-12px"></p>
        </div>
      </div>
      <div
        className={`h-[217px] bg-white my-ando-acc relative${
          acc ? " go-up-acc" : ""
        }`}
      >
        {!dev ? (
          <div
            className="w-full absolute inset-x-0 mx-auto z-10 bg-[rgba(68,68,68)]/30 h-[217px] bottom-0"
            onClick={() => {
              handleClick("acc", false);
            }}
          >
            <div
              className="bg-[#F5F4F1]/70 backdrop-blur-[2px] mt-[72px] w-[248px] h-[75px] rounded-[17px] mx-auto"
              onClick={(e) => {
                e.stopPropagation();
              }}
            >
              <p className="pt-[8px] text-14px text-[#5B5845] font-medium">
                   .
              </p>
              <p
                className="rounded-[16px] bg-[#989384] w-min text-white text-14px font-medium py-[6px] px-[20px] mt-[4px] mx-auto"
                onClick={() => nav("/machine")}
              >
                &nbsp;
              </p>
            </div>
          </div>
        ) : null}

        <Acc
          dev={dev}
          acc_list={acc_list}
          dev_text={dev_text}
          handleClick={handleClick}
        />
      </div>
      <div
        className={`h-[267px] bg-white my-ando-liq${liq ? " go-up-liq" : ""}`}
      >
        {!dev ? (
          <div
            className="absolute z-10 w-full bg-[rgba(68,68,68)]/30 h-[267px] bottom-0 bolt"
            onClick={() => {
              handleClick("liq", false);
            }}
          >
            <div className="bg-[#F5F4F1]/70 backdrop-blur-[2px] mt-[72px] w-[248px] h-[75px] rounded-[17px] mx-auto">
              <p
                className="pt-[8px] text-14px text-[#5B5845] font-medium"
                onClick={(e) => e.stopPropagation()}
              >
                   .
              </p>
              <p
                className="rounded-[16px] bg-[#989384] w-min text-white text-14px font-medium py-[6px] px-[20px] mt-[4px] mx-auto"
                onClick={() => nav("/machine")}
              >
                &nbsp;
              </p>
            </div>
          </div>
        ) : null}

        <Liq
          liq_list={liq_list}
          dev_text={dev_text}
          handleClick={handleClick}
        />
      </div>
    </div>
  );
};

export default memo(MyAndo);
