import { useState } from "react";
import { useNavigate } from "react-router-dom";

// Swiper 
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronUp,
  faChevronDown,
  faChevronRight,
  faPlus,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

export default function Basket() {
  const nav = useNavigate();
  // let parts = new Array(10).fill(0); // 
  let parts = [1, 2, 3];
  const price = 30000; // 
  const myPoint = 13; // 

  const [use_point, setUse] = useState(false);
  const [pointAmount, setAmount] = useState(0);

  const [b_on, setBasket] = useState(false);
  const toggleBasket = () => {
    setBasket((cur) => !cur);
  };

  //    
  const [start_x, setStart] = useState(0);
  const [x_coord, setX] = useState(0);
  const [slide, setSlide] = useState(0);
  const [pressed, setPress] = useState(false);

  return (
    <div className="w-screen max-w-sm bg-[#F5F5F1]">
      <p
        className="font-medium py-[16px]"
        onClick={() => {
          toggleBasket();
        }}
      >
         <FontAwesomeIcon icon={b_on ? faChevronUp : faChevronDown} />
      </p>
      <div
        className="basket overflow-hidden transition-all duration-700"
        //    , "auto"   transition   
        //    height  .
        // "auto"     react-animate-height   .
        style={{ height: b_on ? (parts.length ? 354 : 297) : 0 }}
      >
        {parts.length > 0 ? (
          <div
            onTouchStart={(e) => {
              setStart(e.touches[0].clientX);
              setPress(
                !(
                  e.target.classList.contains("swiper-slide") ||
                  e.target.classList.contains("swiper")
                )
              );
            }}
            onTouchMove={(e) => {
              if (pressed) {
                let sliding = x_coord + e.touches[0].clientX - start_x;
                if (sliding > 90) sliding = 90;
                else if (sliding < 0) sliding = 0;
                setSlide(sliding);
              }
            }}
            onTouchEnd={(e) => {
              if (pressed) setX(slide);
              setPress(false);
            }}
            style={{
              transition: "none",
              transform: `translateX(${slide}px)`,
            }}
          >
            <Swiper
              id="basket"
              width={102}
              slidesOffsetBefore={16}
              slidesOffsetAfter={-16}
              spaceBetween={10}
              modules={[Pagination]}
            >
              {parts.map((part, i) => (
                <SwiperSlide key={i} className="basket-parts">
                  <div className="bg-white w-[28px] h-[28px] rounded-full absolute -top-[8px] -right-[8px] border-4 border-[#F5F5F5] flex justify-center items-center">
                    <FontAwesomeIcon
                      icon={faTimes}
                      className="text-[#B8B8B8]"
                    />
                  </div>
                  {i}
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="mx-[16px] flex justify-between">
              <div
                className="w-[126px] h-[26px] bg-[#5F5F5F] rounded-[5px] flex flex-col justify-center"
                onClick={() => nav("/history")}
              >
                <p className="text-white text-14px">  </p>
              </div>

              <div className="flex">
                <div className="self-center">
                  <p className="w-[19px] h-[19px] bg-[#FFC700] rounded-full flex flex-col justify-center items-center">
                    <img src="img/Ap.png" width={12.44} alt=" " />
                  </p>
                </div>
                &nbsp;
                {!use_point ? (
                  <>
                    <p className="font-bold text-12px self-center">
                      {myPoint.toLocaleString("ko-KR")}p
                    </p>
                    &nbsp;{" "}
                    <form
                      className="flex"
                      onSubmit={(e) => {
                        e.preventDefault();
                        setUse(true);
                      }}
                    >
                      <input
                        className="w-[56px] h-[22px] pl-[6px] bg-[rgba(0,0,0)]/0 text-12px border border-[#b1b1b1] rounded-[3px] self-center"
                        type="number"
                        value={pointAmount}
                        onChange={(e) => {
                          let amount = parseInt(e.target.value);
                          if (amount < 0) amount = 0;
                          if (amount > myPoint) amount = myPoint;
                          setAmount(amount);
                        }}
                      />
                      &nbsp;
                      <p
                        className="text-12px self-center"
                        onClick={() => setUse(true)}
                      >
                        <span></span>
                      </p>
                    </form>
                  </>
                ) : (
                  <>
                    <input
                      className="w-[56px] h-[22px] pl-[6px] bg-[rgba(0,0,0)]/0 text-12px font-bold border border-[#b1b1b1] rounded-[3px] self-center"
                      value={`${pointAmount.toLocaleString("ko-KR")}p`}
                      disabled={true}
                    />
                    &nbsp;
                    <div
                      className="text-12px self-center"
                      onClick={() => setUse(false)}
                    >
                      <span></span>
                    </div>
                  </>
                )}
              </div>
            </div>
            <div className="text-right mt-[8px] mr-[16px] font-semibold">
               {price.toLocaleString("ko-KR")}
            </div>
            <div className="pb-[25px] font-bold text-[#A1ADB7]">
              <p className="w-screen max-w-sm slide-to-buy">
                  {" "}
                <FontAwesomeIcon icon={faChevronRight} className="text-14px" />
                <FontAwesomeIcon icon={faChevronRight} className="text-14px" />
              </p>
            </div>
          </div>
        ) : (
          <>
            <div className="basket-parts w-[102px] flex flex-col mt-[12px] ml-[16px]">
              <p className="text-12px mt-[67.83px]"> !</p>
              <p
                className="mt-[16px] mx-[28px] w-[48px] h-[48px] bg-[#F6F6F6] rounded-full flex justify-center items-center"
                onClick={() => nav("/shop")}
              >
                <FontAwesomeIcon icon={faPlus} />
              </p>
            </div>
            <div className="mx-[16px] flex justify-between pb-[24px]">
              <div
                className="w-[126px] h-[26px] bg-[#5F5F5F] rounded-[5px] flex flex-col justify-center"
                onClick={() => nav("/history")}
              >
                <p className="text-white text-14px">  </p>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
}
