import { useState } from "react";

import HeadShop from "../components/ShoppingPage/HeadShop";

import ShopAll from "../components/ShoppingPage/ShopAll";
import ShopLiquid from "../components/ShoppingPage/ShopLiquid";
import ShopMachine from "../components/ShoppingPage/ShopMachine";
import ShopAccessory from "../components/ShoppingPage/ShopAccessory";

export default function ShopOutline({ shop_index }) {
  const [search_text, setText] = useState("");

  return (
    <>
      <HeadShop setText={setText} />
      {shop_index === 0 ? (
        <ShopAll search_text={search_text} setText={setText} />
      ) : shop_index === 1 ? (
        <ShopLiquid search_text={search_text} setText={setText} />
      ) : shop_index === 2 ? (
        <ShopMachine search_text={search_text} setText={setText} />
      ) : shop_index === 3 ? (
        <ShopAccessory search_text={search_text} setText={setText} />
      ) : null}
    </>
  );
}
