import Axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

const AddressModal = () => {
  return (
    <>
      <div className="py-[20px] ml-[40px] text-left">
        <p>
          <span></span>
          <span className="ml-[3px] font-medium text-[#2454FF]">()</span>
        </p>
        <p className="mt-[5px] font-medium text-14px">
             535 1 101
        </p>
        <p className="mt-[5px] text-14px">02-877-0124</p>
      </div>
      <hr className="border-[#DFDFDF]" />
    </>
  );
};

export default function ReviewPage() {
  const image_base = "https://ando-buck.s3.ap-northeast-2.amazonaws.com";
  const { pid } = useParams();
  const nav = useNavigate();

  const queryResult = { myPoint: 500, amount: 2 };

  const [parts, setParts] = useState([
    {
      liquid_id: "",
      liquid_company: "",
      liquid_name: "",
      liquid_type: "",
      liquid_price: 0,
      liquid_volume: "",
      liquid_sweet: 0,
      liquid_cool: 0,
      liquid_hit: 0,
      liquid_category1: "",
      liquid_category2: "",
      keywords: "",
      item_img: "",
      spec_img: "",
      discount_price: 0,
      favorites: 0,
    },
  ]);

  useEffect(() => {
    //      await  
    async function fetchData() {
      try {
        //      await  
        const res = await Axios.get(
          `http://localhost:5000/api/liqs?liqId=VA_L${pid.padStart(5, "0")}`
        );
        setParts(res.data);
      } catch (e) {
        console.error(e.message);
      }
    }
    fetchData();
  }, [pid]);

  const agreeList = [
    ["()  / ", "/my", "red"],
    ["()  3  ", "/shop", "wheat"],
  ];

  const pow2 = (exp) => Math.pow(2, exp);
  const select_all = pow2(agreeList.length) - 1;

  const [tab, setTab] = useState(0);

  const [use_point, setUse] = useState(false);
  const [pointAmount, setAmount] = useState(0);

  const [agree, setAgree] = useState(0);

  return (
    <>
      <div className="mt-[18px] mx-[16px] tracking-[-5%]">
        <p className="text-left font-medium"> </p>
        <div className="mt-[13px] flex">
          {[" ", " "].map((elem, itr) => (
            <p
              className={`w-6/12 py-[8px] text-14px ${
                tab === itr ? "border-b-[1px] border-black" : null
              }`}
              onClick={() => setTab(itr)}
            >
              {elem}
            </p>
          ))}
        </div>
        <hr className="border-[#9F9F9F]" />

        <AddressModal />

        <div className="my-[20px] text-left">
          <p className="font-medium"> </p>
          <div className="mt-[20px] flex">
            <p className="w-[80px] h-[84px] bg-[#F5F5F5] flex flex-col justify-center items-center">
              <img
                src={image_base + parts[0].item_img}
                width={69.46}
                alt=" "
              />
            </p>
            <div className="ml-[21px]">
              <p className="font-medium">{parts[0].liquid_name}</p>
              <p className="mt-[5px] text-12px text-[#2454FF]">
                {parts[0].liquid_price.toLocaleString("ko-KR")} / {" "}
                {queryResult.amount}
              </p>
            </div>
          </div>
        </div>
        <hr className="border-[#DFDFDF]" />

        <div className="my-[20px] text-left">
          <p className="font-medium"></p>
          <div className="mt-[20px]">
            {!use_point ? (
              <form
                className="flex justify-between"
                onSubmit={(e) => {
                  e.preventDefault();
                  setUse(true);
                }}
              >
                <input
                  className="w-[219px] h-[44px] pl-[12px] text-14px border border-[#E4E4E4] self-center"
                  type="number"
                  value={pointAmount}
                  onChange={(e) => {
                    let amount = parseInt(e.target.value);
                    if (amount < 0) amount = 0;
                    if (amount > queryResult.myPoint)
                      amount = queryResult.myPoint;
                    setAmount(amount);
                  }}
                />
                {!pointAmount ? (
                  <input
                    type="submit"
                    className="w-[98px] h-[44px] text-14px bg-[#F3F3F3] self-center"
                    value=" "
                    onClick={() => {
                      setAmount(queryResult.myPoint);
                      setUse(true);
                    }}
                  />
                ) : (
                  <input
                    type="submit"
                    className="w-[98px] h-[44px] text-14px bg-[#F3F3F3] self-center"
                    value=""
                    onClick={() => setUse(true)}
                  />
                )}
              </form>
            ) : (
              <div className="flex justify-between">
                <input
                  className="w-[219px] h-[44px] pl-[12px] text-14px border border-[#E4E4E4] self-center"
                  value={`${pointAmount.toLocaleString("ko-KR")}p`}
                  disabled={true}
                />
                <button
                  className="w-[98px] h-[44px] text-14px bg-[#F3F3F3] self-center"
                  onClick={() => setUse(false)}
                >
                  
                </button>
              </div>
            )}
          </div>
          <p className="mt-[10px] text-12px">
             {" "}
            <span className="font-medium">
              {queryResult.myPoint.toLocaleString("ko-KR")}p
            </span>
          </p>
        </div>
        <hr className="border-[#DFDFDF]" />

        <div className="my-[20px]">
          <p className="font-medium text-left"> </p>
        </div>
        <hr className="border-[#DFDFDF]" />

        <div className="my-[20px] text-left">
          <p
            onClick={() => {
              console.log("");
              setAgree((cur) => (cur === select_all ? 0 : select_all));
            }}
          >
            {agree === select_all ? (
              <img
                src="/imageDB/common/checked.svg"
                width={16}
                className="inline"
                alt="checked"
              />
            ) : (
              <img
                src="/imageDB/common/unchecked.svg"
                width={16}
                className="inline"
                alt="unchecked"
              />
            )}
            <span className="ml-[12px] text-14px">
                ,    .
            </span>
          </p>
          {agreeList.map((elem, itr) => (
            <p className="ml-[28px]">
              <span
                onClick={() => {
                  setAgree((cur) =>
                    cur & pow2(itr)
                      ? cur & (select_all - pow2(itr))
                      : cur | pow2(itr)
                  );
                }}
              >
                {agree & pow2(itr) ? (
                  <img
                    src="/imageDB/common/checked.svg"
                    width={16}
                    className="inline"
                    alt="checked"
                  />
                ) : (
                  <img
                    src="/imageDB/common/unchecked.svg"
                    width={16}
                    className="inline"
                    alt="unchecked"
                  />
                )}
                <span className="text-12px text-[#747474]">
                  <span className="ml-[12px]">{elem[0]}</span>
                  <button
                    className="px-[5px] underline"
                    onClick={(e) => {
                      e.stopPropagation();
                      nav(elem[1]);
                    }}
                  >
                    
                  </button>
                </span>
              </span>
            </p>
          ))}
        </div>
      </div>

      <p
        className="w-full max-w-sm h-[80px] pt-[16px] font-bold text-white bg-black fixed bottom-0 z-20"
        onClick={() => {
          if (agree === select_all)
            alert(
              parts[0].liquid_price -
                (use_point ? pointAmount : 0) +
                "   ~"
            );
          else alert(" ~");
        }}
      >
        {(
          parts[0].liquid_price * queryResult.amount -
          (use_point ? pointAmount : 0)
        ).toLocaleString("ko-KR")}
         
      </p>
    </>
  );
}
