import { useParams } from "react-router-dom";

export default function ReviewPage() {
  const { pid } = useParams();

  let users = ["*", "*", "*", "*", "*"];
  let profile_pic = [
    "https://bit.ly/3G8EpAP",
    "https://bit.ly/3hKMz9c",
    "https://bit.ly/3hDGbk8",
    "https://bit.ly/3BWLtOy",
    "https://bit.ly/3v6yCFE",
  ];

  let review_logs = [
    ["    .", 3],
    ["    ^^*", 5.0],
    ["     ! ~", 1],
    ["5  10!", 3.5],
    [" .    +   ", 4.62],
  ];

  let review_date = "2022.11.10";

  const Modal = ({ log, itr }) => (
    <>
      <div className="mt-[24px] mx-[16px] flex justify-between">
        <div className="flex">
          <img
            src={profile_pic[itr]}
            className="mx-auto h-[40px] w-[40px]"
            alt="avatar"
          />
          <div className="ml-[15px] text-14px">
            <p className="font-medium">{users[itr]}</p>
            <p className="text-[#878787] Roboto">{review_date}</p>
          </div>
        </div>
        <div>
          <span className="relative text-[#EEEEEE]">
            
            <span
              className="text-[#FF8E00] absolute left-0 overflow-hidden"
              style={{ width: `${log[1] > 5 ? 100 : log[1] * 20}%` }}
            >
              
            </span>
          </span>
        </div>
      </div>

      <div className="mt-[19px] mb-[30px] mx-[16px] bg-[#F6F6F6] py-[12px] px-[12px] text-[12px]">
        {log[0]}
      </div>
    </>
  );

  return (
    <div className="mt-[12px]">
      <p className="ml-[16px] text-left font-medium">
         {review_logs.length.toLocaleString("ko-KR")} 
      </p>
      <hr className="mt-[16px] border-[#EBEBEB]" />
      <div className="text-left">
        {review_logs.map((elem, itr) => (
          <div key={"review_" + pid + "_" + itr}>
            <Modal log={elem} itr={itr} />
            {itr !== review_logs.length - 1 ? <hr /> : null}
          </div>
        ))}
      </div>
    </div>
  );
}
